@font-face {
  font-family: "sgcc";
  src: url("../fonts/sgcc.eot?p5z9ur");
  src: url("../fonts/sgcc.eot?p5z9ur#iefix") format("embedded-opentype"), url("../fonts/sgcc.ttf?p5z9ur") format("truetype"), url("../fonts/sgcc.woff?p5z9ur") format("woff"), url("../fonts/sgcc.svg?p5z9ur#sgcc") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
.sgcc {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "sgcc" !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.sgcc-icon-close:before {
  content: "\e900";
}

.sgcc-icon-cookie:before {
  content: "\e901";
}

.sgcc-main-wrapper {
  position: fixed;
  z-index: 99999999;
  right: 30px;
  bottom: 30px;
  opacity: 1;
  visibility: visible;
  width: 450px;
  background-color: #fbf01e;
  border-radius: 3px;
  transition: opacity 0.2s linear 1s, visibility 0.2s linear 1s;
  -webkit-animation: 0.9s 0.7s sgccfadeinup both;
  animation: 0.9s 0.7s sgccfadeinup both;
  -webkit-box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
  -ms-box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
}

.sgcc-main-wrapper.hidden {
  display: none;
}

.sgcc-main-wrapper .sgcc-cookies {
  position: relative;
  padding: 30px 30px 30px 55px;
}

.sgcc-main-wrapper .sgcc-cookies .cookie-icon {
  position: absolute;
  left: 15px;
  right: auto;
  top: 35px;
  bottom: auto;
  font-size: 26px;
  color: #222222;
}

.sgcc-main-wrapper .message-block {
  margin-bottom: 15px;
}

.sgcc-main-wrapper .sgcc-cookies {
  color: #222222;
}

.sgcc-main-wrapper .sgcc-cookies p {
  font-size: 15px;
  line-height: 1.8;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.sgcc-main-wrapper .sgcc-cookies a {
  display: inline;
  color: #222222;
  text-transform: inherit;
  text-decoration: underline;
  font-weight: normal;
}

.sgcc-main-wrapper .sgcc-cookies a:hover {
  color: #4CC500;
  text-decoration: underline;
  text-transform: inherit;
}

.sgcc-main-wrapper .sgcc-cookies a:focus {
  outline: none;
}

.sgcc-main-wrapper .cookie-compliance-button-block .cookie-compliance-button {
  color: #ffffff;
  background: transparent;
  background-color: #222222;
  border: 1px solid transparent;
  text-decoration: none;
  font-size: 15px;
  font-weight: normal;
  line-height: 1;
  height: auto;
  padding: 12px 20px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
  -ms-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.sgcc-main-wrapper .cookie-compliance-button-block .cookie-compliance-button:focus {
  outline: none;
}

.sgcc-main-wrapper .cookie-compliance-button-block .cookie-compliance-button:hover {
  font-size: 15px;
  font-weight: normal;
  line-height: 1;
  height: auto;
  color: #fffafa;
  background: #4cc500;
  border-color: transparent;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
}

.sgcc-main-wrapper .sgcc-cookies .close {
  position: absolute;
  top: -15px;
  right: -15px;
  display: block;
  width: 30px;
  height: 30px;
  line-height: 34px;
  cursor: pointer;
  text-align: center;
  font-size: 15px;
  color: #ffffff;
  background-color: #333333;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -o-border-radius: 100%;
  -ms-border-radius: 100%;
  border-radius: 100%;
  -webkit-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
  -ms-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.sgcc-main-wrapper .sgcc-cookies .close:focus {
  outline: none;
}

.sgcc-main-wrapper .sgcc-cookies .close:hover {
  color: #ffffff;
  background-color: #4cc500;
}

@media (max-width: 600px) {
  .sgcc-main-wrapper {
    max-width: 400px;
  }
}
@media (max-width: 460px) {
  .sgcc-main-wrapper {
    max-width: 300px;
  }
  .sgcc-main-wrapper .cookie-compliance-button-block .cookie-compliance-button {
    padding: 12px 20px;
  }
}
@media (max-width: 360px) {
  .sgcc-main-wrapper {
    max-width: 270px;
  }
}
@-webkit-keyframes sgccfadeinup {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 20%, 0);
    transform: translate3d(0, 20%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
@keyframes sgccfadeinup {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 20%, 0);
    transform: translate3d(0, 20%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.sgccfadeinup {
  -webkit-animation-name: sgccfadeinup;
  animation-name: sgccfadeinup;
}